import React, { Component } from "react";
import "./App.css";
import TopNav from "./Nav";
import MainArea from "./MainArea";
import Login from "./Login";
import Tickets from "./Tickets";
import Profile from "./Profile";
import { api } from "./services/api";
import { HashRouter, Route } from "react-router-dom";

class App extends Component {
  constructor() {
    super();

    this.state = {
      auth: {
        user: {
          user_id: { id: null, username: null }
        }
      }
    };
  }

  componentDidMount() {
    const token = localStorage.getItem("token");
    if (token) {
      // make a request to the backend and find user
      api.auth.getCurrentUser().then(user => {
        const updatedState = Object.assign({}, this.state.auth, {
          user: { user_id: user }
        });
        this.setState({ auth: updatedState });
      });
    }
  }

  login = (data, action = null) => {
    const updatedState = Object.assign({}, this.state.auth, {
      user: { user_id: data }
    });
    console.log("Current User and Encrypted password", updatedState);
    localStorage.setItem("token", data.jwt);
    this.setState({ auth: updatedState }, () => {
      action();
    });
  };

  logout = () => {
    localStorage.removeItem("token");
    this.setState({ auth: { user: {} } });
  };

  render() {
    return (
      <div className="App">
        <HashRouter basename="/">
          <Route
            path={"/"}
            exact
            render={props => <Login onLogin={this.login} {...props} />}
          />
          <Route
            exact
            path={"/home"}
            render={props => (
              <div>
                <TopNav logout={this.logout} {...props} />
                <MainArea currentUser={this.state.auth.user} {...props} />
              </div>
            )}
          />
          <Route
            path={"/userprofile"}
            render={props => (
              <div>
                <TopNav logout={this.logout} />
                <Profile {...props} currentUser={this.state.auth} />
              </div>
            )}
          />
          <Route
            path={"/tickets"}
            render={props => (
              <div>
                <TopNav logout={this.logout} />
                <Tickets {...props} currentUser={this.state.auth} />
              </div>
            )}
          />
        </HashRouter>
      </div>
    );
  }
}

export default App;
